import axios from "axios"
import moment from "moment"
import common_API from './common.js';

export default {
    keyFun(key){
        key = key.trim();
        let key_len = key.length;
        let str = key.substring(1, key_len -1);
        str = str.toLowerCase();
        return str;
    },
    csv_to_list(fileUrl, has_header_name = false, splitStr = ',', no_header=false){
        return new Promise(function(resolve, reject){
            axios.get(fileUrl, {responseType:'blob'})
            .then((res) => {
                //console.log('read_csv_file res', res);
                let reader = new FileReader();
                reader.readAsText(res.data);
                reader.onload = function(evt){
                    let data = evt.target.result;
                    //console.log('read_csv_file reader data\n', data);
                    let csv_allLines = data.split(/\r?\n|\r/);//
                    //console.log('read_csv_file csv_allLines[0:3]\n', csv_allLines.slice(0,3));
                    let headerNameStr='', headerCodeStr='' ,dataStrList;
                    common_API.is_output_log? console.log(' readCsv has_header_name: ', has_header_name):null;
                    if(no_header){
                        dataStrList = csv_allLines;
                    }else{
                        if(has_header_name){
                            [ headerNameStr, headerCodeStr, ...dataStrList] = csv_allLines;
                        }else{
                            [ headerCodeStr, ...dataStrList] = csv_allLines;
                        }
                    }
                    
                    let headerNameList = has_header_name && headerNameStr? headerNameStr.split(splitStr):[];
                    common_API.is_output_log? console.log(' readCsv reader headerNameList: ', headerNameList):null;
                    let headerCodeList = headerCodeStr? headerCodeStr.split(splitStr):[];
                    common_API.is_output_log? console.log(' readCsv reader headerCodeList: ', headerCodeList):null;
                    common_API.is_output_log? console.log('readCsv reader dataStrList[1:11]\n', dataStrList.slice(0,11)):null;
                    resolve({ headerNameList, headerCodeList, dataStrList});
                }
            }).catch( err =>{
                reject(err)
            });
        });
    },
    read_csv(fileUrl, has_header_name = false, splitStr = ',', keyFun = null, keyArray=null){
        let that = this;
        return new Promise(function(resolve, reject){
            that.csv_to_list(fileUrl, has_header_name, splitStr).then(({ headerCodeList, dataStrList})=>{
                common_API.is_output_log? console.log('read_csv res:', headerCodeList):null;
                let value, csvLineList, csv_line_data, csv_data_list = [];
                dataStrList.forEach((csvLineStr)=>{
                    if(csvLineStr){
                        csv_line_data = {};
                        csvLineList = csvLineStr.split(splitStr)
                        common_API.is_output_log? console.log('read_csv csvLineList:', csvLineList):null;
                        headerCodeList.forEach((key, index)=>{
                            if(keyFun){
                                key = keyFun(key);
                            }
                            value = csvLineList[index];
                            if(keyArray && keyArray.includes(key)){
                                value = Number(value || 0 );
                                value = common_API.toFixed(value, 4)
                            }
                            csv_line_data[key] = value;
                        })
                        common_API.is_output_log? console.log('csv_line_data:', csv_line_data):null;
                        csv_data_list.push(csv_line_data);
                    }
                })
                resolve(csv_data_list)
              }).catch((err) => {
                console.error('read_csv err:', err);
                reject(err)
              })
        });
    },
    save_data_to_csv(data, save_file_name,filterVal = ["date_time","ctm","open","high","low","close","vol"]){
        //console.log('save_data_to_csv data:', common_API.copyObj(data));
        let csvString = filterVal .join();
        csvString += '\r\n';
        let value, length;
        data.forEach((item)=>{
            filterVal.forEach((key)=>{
            value = item[key];
            if(key ==='vol'){
                csvString += value;
            }else{
                csvString += value + ",";
            }
            })
            csvString += "\r\n";
        })
        //console.log('1', csvString)
        csvString = "data:text/csv;charset=utf-8,\ufeff" + encodeURIComponent(csvString);
        let link = document.createElement('a');
        link.href = csvString;
        link.download = save_file_name + ".csv";//
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
    },
}