import common_API from './common.js';
import moment from "moment"

export default {
  find_in_time(date_list, oneDay){
    let i, date, date_val_1, date_val_2, startIdx=-1, is_fond = false;
    // oneDay
    for(i= date_list.length-1; i>=0; i--){
      date = date_list[i];
      common_API.is_output_log && i <3? console.log('i: ' + i +' date', date):null;
      let bool1 = moment(date).format("YYYY-MM-DD") == moment(oneDay).format("YYYY-MM-DD");
      let bool2 = moment(date).valueOf() <= moment(oneDay).valueOf();
      // date = moment(date_list[i]).format("YYYY-MM-DD");
      // date_val_1 = moment(date).valueOf();
      // oneDay = moment(oneDay).format("YYYY-MM-DD");
      // date_val_2 = moment(oneDay).valueOf();
      // let bool3 = date_val_1 <= date_val_2;
      if(!is_fond && (bool1 || bool2)){
        //console.log('is_fond i:', i ,'oneDay', oneDay, ' date:' ,date);
        is_fond = true;
        startIdx = i;
      }
    }
    //console.log('startIdx', startIdx, 'oneDay', oneDay, ' date_list', date_list);
    return startIdx;
  },
  boll_open(upper_list,lower_list,idx_list){
    let lower, diff, boll_range = [];
    upper_list.forEach((item,index)=>{
      lower = lower_list[index]
      diff = item - lower;
      diff = common_API.toFixed(diff,5)
      boll_range.push(diff)
    })
    //console.log('boll_range', boll_range);
    let open = this.price_rise(boll_range,idx_list)
    return open;
  },
  //price_rise
  get_indicate_price_rise2(price_list = [2,1,0,3,4,5,4,7,8,10]) {
    let rise_list = [];
    let diff, dn;
    let val_day_num = 365/7 * 5;
    price_list.forEach(function(item, index) {//10
      if (index ==0 ){//0,1,2
        rise_list[index] = 0;
      }else {//index=3,,,,,,,
        dn = price_list[index -1]
        diff = price_list[index] - dn;
        if(dn > 0){
          rise_list[index] = common_API.toFixed(diff/dn * 100 * val_day_num, 1);
        }else{
          rise_list[index] = 0;
        }
      }
    })
    return rise_list;
  },
  //idx_list = [3,2,1] 3
  //idx_list = [7,5,3,1] 5
  //max - min
  //,-1
  price_rise_rate(price_list,idx_list){
    //console.log('ma_rise', common_API.copyObj(price_list));
    let rise_rate = 0;
    let price_len = price_list.length;
    if(idx_list[0] > price_len){
      console.error('price_rise idx_list ');
      return
    }
    let m,n,go_rise = true;
    let idx_list_len = idx_list.length;
    for(let i=0; i<idx_list_len-1; i++){
      m = idx_list[i];//3
      n = idx_list[i + 1];//2
      //console.log('m',m, n, price_list[price_len - m] > price_list[price_len - n]);
      //list[3] > list[2] 
      if(price_list[price_len - m] > price_list[price_len - n]){
        go_rise = false;
      }
    }
    let go_down = true;
    for(let i=0; i<idx_list_len-1; i++){
      m = idx_list[i];//3
      n = idx_list[i + 1];//2
      //list[3] < list[2] 
      if(price_list[price_len - m] < price_list[price_len - n]){
        go_down = false;
      }
    }
    m = idx_list[0];//3
    n = idx_list[idx_list_len -1];//1
    let dn = price_list[price_len - n];
    let dm = price_list[price_len - m];// list[3]
    let diff = dn - dm;//list[1] - list[3]
    let rate = 0;
    let val_day_num = 365/7 * 5;
    let day_num = m - n ;
    if(dm >0){
      rate = common_API.toFixed(diff/dn * 100 /day_num * val_day_num, 1)
    }
    if(go_rise){//
      rise_rate = rate;
    }else if(go_down){//
      rise_rate = rate;
    }else{//
      rise_rate = -999;
    }
    return rise_rate;
  },
  get_list_1_2_yearly_rate(list1, list2, k_obj, type){
    if(list1.length != list2.length){
      console.warn(k_obj.symbol + '  ' + type + ' list1, list2',k_obj);
      return -999;
    }
    if(list1.length <2){
      console.warn(k_obj.symbol  + '  ' + type +' list1',k_obj);
      return -999;
    }
    if(list2.length <2){
      console.warn(k_obj.symbol  + '  ' + type +' list2',k_obj);
      return -999;
    }
    let yearly_rate = 0;
    let length = list1.length;
    let today_ma = list1[length - 1] - list2[length - 1]; //ma
    let yesterday_ma = list1[length - 2] - list2[length - 2]; //ma
    let val_day_num = 365/7 * 5;
    if(yesterday_ma && yesterday_ma >0){
      let day3_rate = (today_ma - yesterday_ma)/ yesterday_ma;
      if(type.startsWith('boll_')){
        day3_rate = day3_rate/4;
      }
      yearly_rate = day3_rate * val_day_num;
      yearly_rate = yearly_rate * 100;
      yearly_rate = common_API.toFixed(yearly_rate, 3);
    }
    return yearly_rate;
  },
  get_yearly_rate(ma_list){
    let yearly_rate = 0;
    let length = ma_list.length;
    if(length<2){
      console.warn('get_yearly_rate ma_list');
      return -999;
    }
    let today_ma = ma_list[length - 1]; //ma
    let yesterday_ma = ma_list[length - 2]; //ma
    let val_day_num = 365/7 * 5;
    if(yesterday_ma && yesterday_ma >0){
      let day3_rate = (today_ma - yesterday_ma)/ yesterday_ma;
      yearly_rate = day3_rate * val_day_num;
      yearly_rate = yearly_rate * 100;
      yearly_rate = common_API.toFixed(yearly_rate, 3);
    }
    return yearly_rate;
  },
  get_indicate_yearly_rate(price_list = [2,1,0,3,4,5,4,7,8,10]) {
		let rise_list = [];
    let that = this;
    price_list.forEach(function(item, index) {//10
			if (index < 1){//0
        rise_list[index] = 0;
      }else {//index=1
        let temp_list = price_list.slice(index - 1, index + 1);//slice(0,3)
        rise_list[index] = that.get_yearly_rate(temp_list)
			}
		})
		return rise_list;
	},
  //boll_open
  get_indicate_boll_open(upper_list,lower_list,idx_list){
    let lower, diff, stdev, boll_range = [];
    upper_list.forEach((item,index)=>{
      lower = lower_list[index]
      diff = item - lower;
      stdev = common_API.toFixed(diff/4 ,5)
      boll_range.push(stdev)
    })
    //console.log('boll_range', boll_range);
    let boll_open_list = this.get_indicate_price_rise(boll_range,idx_list)
    return boll_open_list;
  },
  //boll_open
  get_indicate_2ma_open(ma1_list,ma2_list,idx_list){
    let lower, diff, stdev, boll_range = [];
    ma1_list.forEach((item,index)=>{
      lower = ma2_list[index]
      diff = item - lower;
      stdev = common_API.toFixed(diff ,5)
      boll_range.push(stdev)
    })
    //console.log('boll_range', boll_range);
    let boll_open_list = this.get_indicate_price_rise(boll_range,idx_list)
    return boll_open_list;
  },
  //price_rise
  get_indicate_price_rise(price_list = [2,1,0,3,4,5,4,7,8,10], idx_list= [3,2,1] ) {
		let rise_list = [];
    let maxIdx = idx_list[0];//3
    let that = this;
    price_list.forEach(function(item, index) {//10
			if (index < maxIdx-1){//0,1,2
        rise_list[index] = -999999;
      }else {//index=3,,,,,,,
        let idx2 = index + 1;
        let idx1 = idx2 - maxIdx;
        let temp_list = price_list.slice(idx1, idx2);//slice(0,3)
        //index=4  slice(1,4)
        //index=5  slice(2,5)
        //.......
        //index=9  slice(7,10)
        //console.log('index ',index,' temp_list:', temp_list);
        rise_list[index] = that.price_rise_rate(temp_list,idx_list)
			}
		})
		return rise_list;
	},
  get_ma(close_list, period=20) {
		let ma_list = [];
    close_list.forEach(function(item, index) {
			if (index < period-1){
        ma_list[index] = '';
      }else {//index=19,,,,,,,(>= period 20)
        let sum = 0;
				for (let j = 0; j < period; j++){
          sum += close_list[index - j];
          //index = 19
          //j      :  0,1,2,3,,,,,,19
          //19 - j : 19,18,17,,,,,,0
          //sum = close 0-19
          //index = 20
          //j      :  0,1,,,,,,,19
          //20 - j : 20,19,,,,,,,1
          //sum = close 1-20
          //..........
          //index = 40
          //j      :  0,1,,,,,,,19
          //40 - j : 40,39,,,,,,,21
          //sum = close 21-40
        } 
        let ma = sum / period;
				ma_list[index] = Number(ma.toFixed(3) || 0);
			}
		})
		return ma_list;
	},
  //t--close_list, e--period; n--dev
  get_boll(t, e, n) {
    // = 20 
    //Up  = 20  + 2SD20 
    //Down  =20  - 2SD20 )
		void 0 === e && (e = 20),
		void 0 === n && (n = 2);
		var i = [[], [], []],
		a = i[0],
		r = i[1],
		o = i[2],
		s = [0, 0],
		c = s[0],
		l = s[1],
		u = e - 1;
    let ma = 0,up=0, down = 0;
    t.forEach(function(i, s) {
			c += i;
			var d = [0, 0, 0, 0],
			f = d[0],
			h = d[1],
			p = d[2],
			_ = d[3];
			s >= u ? (c -= l, l = t[_ = s - u], f = c / e, p = t.slice(_, _ + e).reduce(function(t, e) {
				return t + Math.pow(e - f, 2)
			},
			0), h = Math.sqrt(p / e),
      ma = f,
      r[s] = Number(ma.toFixed(5) || 0),  
      up = f + n * h,
      a[s] = Number(up.toFixed(5) || 0), 
      down = f - n * h,
      o[s] = Number(down.toFixed(5) || 0)
      ) : (a[s] = 0, r[s] = 0, o[s] = 0)
		})
    a.forEach((item,index)=>{
      if(item == 0){
        a[index] = '';
      }
    });
    r.forEach((item,index)=>{
      if(item == 0){
        r[index] = '';
      }
    });
    o.forEach((item,index)=>{
      if(item == 0){
        o[index] = '';
      }
    });
		return { up: a, ma: r, low: o }
	},
}