<template>
  <div class="HisReplay">
    <!-- <van-button type="primary" @click="tdx_15_to_day" round >tdx 15</van-button>
    <van-button type="primary" @click="tbj_15_to_day" round >tbj 15</van-button>
    <van-button type="primary" @click="jjsj_15_to_day" round >jjsj 15</van-button>-->
    <!-- <van-button type="primary" @click="get_day_obj_list_csv" round >day_vibrate </van-button> -->
    <!-- <van-button type="primary" @click="xgsj_5_to_day" round >xgsj 5</van-button>
    <van-button type="primary" @click="save_data" round >csv</van-button> -->
    <div class="title-tag"></div>
    <div>
      <p>30K</p>
    </div>
    <br/>
    <div class="title-tag"></div>
    <van-cell is-link @click="datePopupShow = true" title="" :value="select_day_str"></van-cell>
    <van-popup 
      v-model="datePopupShow" 
      position="top" 
      :style="{ height: '50%' }" 
      get-container="body">
      <van-datetime-picker
        v-model="selected_day"
        type="date"
        title=""
        :min-date="minDate"
        :max-date="maxDate"
        @confirm="datePopupShow = false"
        @cancel="datePopupShow = false"
      />
    </van-popup>
    <br/>
    <van-row>
      <van-col span="12">
        <van-button type="primary" @click="forecast_before_10" size="small">105</van-button>
      </van-col>
      <van-col span="12">{{day_5_for_val}}</van-col>
    </van-row>
    <van-divider />
    <van-row>
      <van-col span="9">
        <van-button type="info " @click="forecast_after_10" size="small">10</van-button>
      </van-col>
      <van-col span="15">: {{forecast1}}  -- {{forecast2}}</van-col>
    </van-row>
    <br/>
    <div class="title-tag"></div>
    <van-row>
      <van-col span="12"><span></span><input class="h1_ohlc_input" v-model="h1_open" @blur="onOHLCChange"/></van-col>
      <van-col span="12"><input class="h1_ohlc_input" v-model="h1_close" @blur="onOHLCChange"/></van-col>
    </van-row>
    <van-row>
      <van-col span="12"><input class="h1_ohlc_input" v-model="h1_high" @blur="onOHLCChange"/></van-col>
      <van-col span="12"><input class="h1_ohlc_input" v-model="h1_low"  @blur="onOHLCChange"/></van-col>
    </van-row>
    <van-divider />
    <van-row>
      <van-col span="12">{{h1_cost}}</van-col>
      <van-col span="12">{{h1_color_str}}</van-col>
    </van-row>
    <van-row>
      <van-col span="12">{{stand_h1_colse}}</van-col>
      <van-col span="12">{{trade_direction_str}}</van-col>
    </van-row>
    <van-divider />
    <div class="title-tag"></div>
    <van-row>
      <van-col span="12">{{profit_loss_str}}</van-col>
      <van-col span="12">{{h1_test_str}}</van-col>
    </van-row>
    <div>
      <p>{{score_str}}</p>
      <p>5{{target0}}</p>
      <p>: {{target1}}  -- {{target2}}</p>
      <p>{{day_low}} -- {{day_high}}</p>
    </div>
    
  </div>
</template>
<script>
import moment from "moment"
import k_handel_API from '@/utils/k_handel.js';
import common_API from '@/utils/common.js';
import csv_API from '@/utils/csv.js';
import m15_to_day_Mixin from "./m15_to_day_Mixin.js";
import forecast_Mixin from "./forecast_Mixin.js";
import Vue from 'vue'
import { Toast } from 'vant';
Vue.use(Toast);
const Big = require('big.js')
export default {
  name: 'HisReplay',
  mixins: [m15_to_day_Mixin, forecast_Mixin],
  data() {
    return {
      datePopupShow:false,
      symbol:'MAM',
      ma_period: 222,//2221234;//5
      today: new Date(),
      minDate: new Date('2012/1/9'),
      maxDate: new Date('2022/7/20'),
      selected_day: new Date('2012/10/10'), //  2012/9/20
      selected_day_week:'',
      selected_day_obj: null,
      past_list: [],
      day_5_for_val: 0,
      day_obj_list:[],
      day_high:0,
      day_low:0,
      day_vibrate:0,
      h1_open:0,
      h1_high:0,
      h1_low:0,
      h1_close:0,
      h1_cost:0,
      h1_color:0,//01-1
      trade_direction:0,//0,1-1
      stand_h1_colse:0,//
      h1_test_str:'',
      forecast1:0,
      forecast2:0,
      forecast_aver:0,
      for12_R:0,
      profit_loss:0,//-11
      score:0,//1,min,  2:minaver 3:avermax;  4max
      target0:0,
      target1:0,
      target2:0,
      h1_vibrate_range_list:[],
      h1_vol_range_list:[],
    };
  },
  computed: {
    h1_color_str: function () {
      let str = '';
      if(this.h1_color === 1){
        str = '';
      }
      if(this.h1_color === -1){
        str = '';
      }
      return str;
    },
    trade_direction_str: function () {
      let str = '';
      if(this.trade_direction === 1){
        str = '';
      }
      if(this.trade_direction === -1){
        str = '';
      }
      return str;
    },
    profit_loss_str: function () {
      let str = '';
      if(this.profit_loss === 1){
        str = '';
      }
      if(this.profit_loss === -1){
        str = '';
      }
      return str;
    },
    score_str: function () {
      let str = '';
      let list = ['','min','minaver','avermax','max'];
      return list[this.score];
    },
    select_day_str: function () {
      let str = '';
      if(this.selected_day){
        str = this.selected_day.toLocaleDateString();
      }
      if(this.selected_day_week){
        str += ' ' + this.selected_day_week;
      }
      return str;
    },
  },
  mounted () {
    common_API.longLog();
    //this.get_p64_list()
    this.get_day_obj_list_csv();
    //this.get_p49_list();
  },
  methods: {
    get_day_obj_list_csv(){
      let save_file_name = 'xgsj_MAM_day_obj.csv';
      let list = ['h1_vol','h1_vibrate','day_vibrate','day_high','day_low','h1_high','h1_low','h1_cost','h1_open','h1_close'];
      csv_API.read_csv(save_file_name, false,',',null, list).then((res)=>{
        console.log('day_obj res1:', common_API.copyObj(res));
        this.day_obj_list = res;
        //this.statistics(res)
        this.forecast_before_10();
        this.forecast_after_10();
      }).catch((err) => {
        console.error('get_his_data err:', err);
      })
    },
    forecast_before_10(){
      let week_day_idx = new Date(this.selected_day).getDay()
      let week_day_list = ['','','','','','',''];
      this.selected_day_week = ''+ week_day_list[week_day_idx]
      console.log('this.selected_day:', new Date(this.selected_day).toLocaleDateString(), ' ' + this.selected_day_week);
      let selected_day_idx = -1;
      let len = this.day_obj_list.length, item;
      for(let i=0; i<len; i++){
        item = this.day_obj_list[i];
        if(item.day === new Date(this.selected_day).toLocaleDateString()){
          selected_day_idx = i;
          break;
        }
      }
      if(selected_day_idx >=0){
        this.select_day_obj = this.day_obj_list[selected_day_idx];
        this.past_list = this.day_obj_list.slice(0, selected_day_idx)
        console.log('select_day_obj', common_API.copyObj(this.select_day_obj));
        console.log('past_day_list', common_API.copyObj(this.past_list));
        this.day_5_forecast(this.past_list);
      }else{
        console.log('');
        Toast.fail('');
      }
    },
    forecast_after_10(){
      let h1_vibrate = this.select_day_obj.h1_vibrate;
      let h1_vol = this.select_day_obj.h1_vol;
      let day_vibrate = this.select_day_obj.day_vibrate;
      let day_high = this.select_day_obj.day_high;
      let day_low = this.select_day_obj.day_low;
      let day = new Date(this.selected_day).toLocaleDateString()
      let h1_high = this.select_day_obj.h1_high;
      let h1_low = this.select_day_obj.h1_low;
      let h1_open = this.select_day_obj.h1_open;
      let h1_close = this.select_day_obj.h1_close;
      let h1_cost = this.select_day_obj.h1_cost;
      this.h1_open = h1_open;
      this.h1_high = h1_high;
      this.h1_low = h1_low;
      this.h1_close = h1_close;
      this.h1_cost = h1_cost;
      this.day_high = day_high;
      this.day_low = day_low;
      this.day_vibrate = day_vibrate;
      this.handle_h1_forecast(h1_vibrate, h1_vol, this.past_list, day_vibrate, day, h1_high, h1_low, h1_cost, h1_open, h1_close, day_high,day_low);
      // this.h1_vol_range_list = this.deal_hz_vol(past_list, 25000)
      // this.h1_vibrate_range_list = this.deal_hz_vibrate(past_list)
    },
    statistics(res){
      console.log('ma_period:', this.ma_period);
      let item, list, day, h1_vibrate, h1_vol, day_vibrate;
      let for_diff, for12_R;
      let num= new Big(0), sum1 = new Big(0), sum2 = new Big(0);
      let len = res.length;
      for(let i = 80; i<85; i++){
        item = res[i];
        day = item.day;
        h1_vibrate = item.h1_vibrate;
        h1_vol = item.h1_vol;
        day_vibrate = item.day_vibrate;
        list = res.slice(0, i);
        //console.log('i', common_API.copyObj(res[i]));
        ({ for_diff, for12_R } = this.handle_h1_forecast(h1_vibrate, h1_vol, list, day_vibrate, day));
        num = num.plus(1);
        sum1 = sum1.plus(Math.abs(for_diff));
        sum2 = sum2.plus(Math.abs(for12_R));
      }
      Big.DP = 2
      let f_aver = sum1.div(num);
      let R_aver = sum2.div(num);
      console.log('num:', num.toFixed(0), ' f_aver:', f_aver.toFixed(2), ' R_aver:', R_aver.toFixed(2));
    },
    get_p49_list(){
      let save_file_name = 't2_book_p49_list.csv'
      csv_API.read_csv(save_file_name, false,',',null,['day_vibrate','h1_vibrate','h1_vol']).then((res)=>{
        console.log('get_p49_list res1:', common_API.copyObj(res));
        this.handle_h1_forecast(77, 68184, res);
      }).catch((err) => {
        console.error('get_p49_list err:', err);
      })
    },
    handle_h1_forecast(h1_vibrate, h1_vol, k_list, day_vibrate=0, day, h1_high, h1_low, h1_cost, h1_open, h1_close, day_high,day_low){
      //console.log('10past_day_list:', common_API.copyObj(k_list));
      let h1_vol_list=[], h1_vibrate_list=[];
      let h1_vol_diff, h1_vibrate_diff, h1_vol_diff_abs, h1_vibrate_diff_abs;
      k_list.forEach((item)=>{
        h1_vol_diff = item.h1_vol - h1_vol;
        h1_vol_diff_abs = Math.abs(h1_vol_diff)
        h1_vibrate_diff = item.h1_vibrate - h1_vibrate;
        h1_vibrate_diff_abs = Math.abs(h1_vibrate_diff)
        h1_vol_list.push({...item, h1_vol_diff, h1_vol_diff_abs})
        h1_vibrate_list.push({...item, h1_vibrate_diff, h1_vibrate_diff_abs})
      })
      this.forecast1 = this.handle_h1_vibrate_list(h1_vibrate_list)
      this.forecast2 = this.handle_h1_vol_list(h1_vol_list)
      let sum = common_API.toFixed(this.forecast1 + this.forecast2,2);
      this.forecast_aver = common_API.toFixed(sum/2,2);
      this.for12_R = common_API.toFixed(Math.abs((this.forecast1 - this.forecast_aver)),2);
      console.log(':', day, '\t: ', h1_vibrate, '\tvol:', h1_vol, 
        '\t: r1:', this.forecast1, '\tr2:', this.forecast2, '\taver', this.forecast_aver, '\tR:', this.for12_R ,);
      let for_diff = common_API.toFixed((this.forecast_aver - day_vibrate),2)
      this.onOHLCChange();
      //console.log('day:', day, '\tr1:', this.forecast1, '\tr2:', this.forecast2, '\tR:', this.for12_R , '\taver', this.forecast_aver, '\treal:', real, '\tdiff:', for_diff);
      return
    },
    onOHLCChange(){
      this.h1_open = +this.h1_open;
      this.h1_high = +this.h1_high;
      this.h1_low = +this.h1_low;
      this.h1_close = +this.h1_close;
      let sum = this.h1_high + this.h1_low;
      this.h1_cost = common_API.toFixed(sum/2, 2);
      console.log('onOHLCChange open:', this.h1_open, '\thigh:', this.h1_high, '\tlow:', this.h1_low, '\tclose:', this.h1_close, '\tcost', this.h1_cost);
      if(this.h1_close > this.h1_open){//
        this.h1_color = 1;
        this.stand_h1_colse = this.h1_close > this.h1_cost;//
        this.trade_direction = this.stand_h1_colse? 1:0;
      }
      if(this.h1_close < this.h1_open){//
        this.h1_color = -1;
        this.stand_h1_colse = this.h1_close < this.h1_cost;//
        this.trade_direction = this.stand_h1_colse? -1:0;
      }
      let h1_colse_vibrate, min, max;
      min = Math.min(this.forecast1, this.forecast2)//
      max = Math.max(this.forecast1, this.forecast2)//
      if(this.trade_direction ===1){//
        h1_colse_vibrate = this.h1_close - this.h1_low;
        if(this.h1_low === this.day_low){
          this.h1_test_str = '';
        }
        this.target0 = this.h1_low +  this.day_5_for_val;
        this.target1 = this.h1_low +  min;
        this.target2 = this.h1_low +  max;
      }
      if(this.trade_direction ===-1){//
        h1_colse_vibrate = this.h1_high - this.h1_close;
        if(this.h1_high === this.day_high){
          this.h1_test_str = '';
        }
        this.target0 = this.h1_high -  this.day_5_for_val;
        this.target1 = this.h1_high - max;
        this.target2 = this.h1_high -  min;
      }
      if(this.trade_direction !==0){
        if(this.day_vibrate < h1_colse_vibrate){
          this.profit_loss = -1;//-1,10,
        }
        if(this.day_vibrate > h1_colse_vibrate){
          this.profit_loss = 1;//1,10,
        }
        if(this.day_vibrate < min){
          this.score = 1
        }else if(this.day_vibrate < this.forecast_aver){
          this.score = 2;
        }else if(this.day_vibrate < max){
          this.score = 3;
        }else{//max
          this.score = 4;
        }
      }
      console.log('', this.h1_test_str, '\t profit_loss-11:', this.profit_loss, '\t score1,min,  2:minaver 3:avermax;  4max:', this.score);
    },
    get_p64_list(){
      let save_file_name = 't2_book_p64_list.csv'
      csv_API.read_csv(save_file_name, false,',',null,['day_vibrate']).then((res)=>{
        console.log('get_p64_list res1:', common_API.copyObj(res));
        this.deal_day_vibrate(res);
      }).catch((err) => {
        console.error('get_p64_list err:', err);
      })
    },
  }
}
</script>
<style scoped>
.echarts-view{
  height: 300px;
  width:  100%;
}
.small_width{
  width: 100px;
}
.HisReplay{
  padding: 5px 10px;
}
.title-tag{
  font-size: 18px;
  font-weight: bold;
}
.h1_ohlc_input{
  display: inline-block;
}
</style>