import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

import { Button,Switch,Field,Cell, CellGroup,DatetimePicker,Popup, Col, Row,Divider  } from 'vant';
Vue.use(Button);
Vue.use(Switch);
Vue.use(Field);
Vue.use(Cell);
Vue.use(CellGroup);
Vue.use(DatetimePicker);
Vue.use(Popup);
Vue.use(Col);
Vue.use(Row);
Vue.use(Divider);

Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
