export default {
  is_in_company: true,
  is_output_log: false,
  his_date_date:'2021_12_20',
  show_need_bar_num : 80, // bollbar
  longLog(){
    console.log('-------------------------------------------------------------------------------');
  },
  copyObj(source){
    return JSON.parse(JSON.stringify(source || ''))
  },
  toFixed(val,digit){
    let v1 = Number(val || 0);
    let v2 = v1.toFixed(digit);
    let v3 =  Number(v2|| 0);
    return v3
  },
  openInTV(exchangeSymbol){
    let list = exchangeSymbol.split(':');
    let exchange = list[0];
    let symbol = list[1];
    let url= `https://cn.tradingview.com/chart/0ZToVShK/?symbol=${exchange}%3A${symbol}`;
    window.open(url);
  },
}