      for(let i=0; i<h1_v_len; i++){
        num_sum += h1_vibrate_diff_abs_obj_list[i].num;
        day_vibrate_sum += h1_vibrate_diff_abs_obj_list[i].day_vibrate_sum;
        if(num_sum > select_num){
          //console.log('i:', i, ' item:', common_API.copyObj(h1_vibrate_diff_abs_obj_list[i]));
          break;
        }
      }
      let day_vibrate_aver = common_API.toFixed(day_vibrate_sum/num_sum,1);
      //console.log('num_sum', num_sum, ' day_vibrate_aver:', day_vibrate_aver);
