    get_day_list(k_list){
      let day_list = [];
      k_list.forEach((item)=>{
        if(!day_list.includes(item.date)){
          day_list.push(item.date)
        }
      })
      console.log('day_list:', day_list);
      let day_obj_list=[];
      let last_day, obj, list=[];
      day_list.forEach((day_str)=>{
        list=[];
        k_list.forEach((item)=>{
          if(last_day && item.date === last_day){
            if(item.hour >15){
              list.push(item);
            }
          }
          if(item.date === day_str){
            if(item.hour <=15){
              list.push(item);
            }
          }
        })
        last_day = day_str;
        let { open, high, low, close, vol } = this.handle_day_ohlc(list);
        obj = {
          day:day_str+ ' 15:00:00',
          list,
          open, 
          high, 
          low, 
          close, 
          vol
        }
        day_obj_list.push(obj)
      })
      console.log('day_obj_list', common_API.copyObj(day_obj_list));
      let save_file_name = this.symbol + '_d1'
      csv_API.save_data_to_csv(day_obj_list, save_file_name,['day','open','high','low','close','vol'])
    },
    handle_day_ohlc(k_list){
      let open=0,close=0,vol = 0;
      let k_len = k_list.length;
      if(k_list[0] && k_list[0].open){
        open = k_list[0].open;
      }
      if(k_len-1 >= 0 && k_list[k_len-1] && k_list[k_len-1].close){
        close = k_list[k_len-1].close
      }
      let high_list = [],low_list = [];
      k_list.forEach((item)=>{
        high_list.push(item.high);
        low_list.push(item.low);
        vol += item.vol;
      })
      let day_high,day_low;
      if(high_list.length){
        day_high = Math.max.apply(null,high_list);
      }
      if(low_list.length){
        day_low = Math.min.apply(null,low_list);
      }
      //console.log('day_vibrate', day_vibrate, ' day_high', day_high, ' day_low', day_low);
      return {
        open,
        high: day_high, 
        low: day_low,
        close,
        vol, 
      }
    },
    data_format2(h1_list){
      let date_obj, date, time, date_time;
      h1_list.forEach((item)=>{
        date_obj = new Date(item.t_time);
        date = date_obj.toLocaleDateString();
        time = date_obj.toTimeString().slice(0,5);
        date_time = date + ' ' + time;
        item.date = date
        item.time = time
        item.week_day = date_obj.getDay();// 0  6
        item.date_time = date_time;
        item.year = date_obj.getFullYear();
        item.hour = date_obj.getHours();
      })
      return h1_list
    },
